export default {
	namespaced: true,
	state: {
		products: getProducts()
	},
	getters: {
		all(state){
			return state.products;
		},
		getId: (state, getters) => (id) => {
			return state.products[id];
		}
	},
	mutations: {
		some(state, id){
			// state.products[id]; // ....
		}
	},
	actions: {
		some(store){}
		}
};


function getProducts(){
	return {
		1: {
			id_product: 1,
			title: 'Iphone 5',
			price: 5000,
			image: 'https://picsum.photos/800/400/?image=816'
		},
		2: {
			id_product: 2,
			title: 'Iphone 6',
			price: 10000,
            image: 'https://picsum.photos/800/400/?image=504'
		},
		5: {
			id_product: 5,
			title: 'Iphone 7',
			price: 30000,
            image: 'https://picsum.photos/800/400/?image=367'
		}
	}
}

